            </el-col>
            <el-col :span='12'>
              <el-form-item label="剩余花深" prop="surplus_flowerdeep" :rules="{required: true, validator: (rule, value, callback) => checkPositive(rule, value, callback,'剩余花深'), trigger: 'change' }">
                <el-input type="text" :disabled="type === 3" v-model="params.surplus_flowerdeep" @input="computeDeep('surplus_flowerdeep')" @change="changeComputeDeep(params, 'surplus_flowerdeep')" />
