     trigger: 'change' }">
                <el-input type="text" :disabled="type === 3" v-model="params.factory_flowerdeep" @input="computeDeep('factory_flowerdeep')" @change="changeComputeDeep(params, 'factory_flowerdeep')" />
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="公司照顾花深" prop="care_flowerdeep" :rules="{ validator: (rule, value, callback) => checkPositive(rule, value, callback,'公司照顾花深'),
     trigger: 'change' }">
                <el-input type="text" :disabled="type === 3" v-model="params.care_flowerdeep" @input="computeDeep('care_flowerdeep')" @change="changeComputeDeep(params, 'care_flowerdeep')" />
