                <el-input type="text" :disabled="type === 3" v-model="params.mm_wearprice" @input="computeDeep('mm_wearprice')" @change="changeComputeDeep(params, 'mm_wearprice')" />
              </el-form-item>
            </el-col>
          </el-row>
