<script>
import { computeDeep, computePrice, sumbitRowModule } from "./company_Add";
export default {
  name: "aftersaleCompanyModification",
  data() {
    return {
      authority: {},
      TableData: {
        info_data: {},
        list: []
      },
      timeValue: [],
      params: {
        symptom_id: []
      },
      register_id: null
    };
  },
  props: ["type"], // type = 1 是 修改 type 2是变更 3 查看 4 照顾
  methods: {
    // 检验整数
    checkPositive(rule, value, callback, str) {
      var result = isNaN(Number(value)) ? 0 : Number(value).toFixed(2);
      if (isNaN(Number(value)) || result < 0) {
        callback(new Error(str + "必须是大于等于0的数字"));
      }
      callback();
    },
    // 保留两位小数
    changeComputeDeep(row, value) {
      this.$publicFution.toFixedTwo(row, value);
      this.computeDeep(value);
    },
    // 计算深度
    computeDeep(value) {
      computeDeep({ row: this.params, column: { property: value } });
    },
    // 保留两位小数
    changeComputePrice(row, value) {
      this.$publicFution.toFixedTwo(row, value);
      this.computePrice(value);
    },
    // 计算价格
    computePrice(value) {
      computePrice({ row: this.params, column: { property: value } });
    },
    // 仓库改变
    warehouseChange() {
      this.$set(this.params, "allocation_id", null);
      this.$set(this.params, "allocation_name", null);
    },
    goBack() {
      this.$router.push({
        path: "/aftersale/companyjudgement"
      });
    },
    audit(api) {
      //审核和作废
      this.$axMethod[api]({
        company_appraisal_goods_id: this.orderId
      }).then(response => {
        if (response.data.code == 200) {
          this.$message({
            message: response.data.msg ? response.data.msg : response.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    submit() {
      this.$refs.elForm.validate(result => {
        if (result) {
          var submitData = this.$publicFution.deepCopy(this.params);
          submitData.symptom_id = Array.isArray(submitData.symptom_id)
            ? submitData.symptom_id.join(",")
            : submitData.symptom_id;
          var params = {
            company_appraisal_goods_id: submitData.company_appraisal_goods_id,
            business_time: submitData.business_time,
            company_id: submitData.company_id,
            inspector_id: submitData.inspector_id
          };
          Object.assign(
            params,
            this.$publicFution.filter(sumbitRowModule, submitData)
          );
          var url;
          if (this.type === 1) {
            url = "editCompanyAppraisal";
          } else if (this.type === 2) {
            url = "DataChangesCompanyAppraisal";
          } else if (this.type === 4) {
            url = "CareCompanyAppraisal";
          }
          this.$axMethod[url](params).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.msg ? res.data.msg : res.data.data,
                type: "success"
              });
              this.goBack();
            }
          });
        }
      });
    }
  },
  created() {
    this.EmployeeListInvok();
    this.getNEwlist();
    if (this.$route.query && this.$route.query.id) {
      this.orderId = this.$route.query.id;
      this.$axMethod
        .infoCompanyAppraisal({
          company_appraisal_goods_id: this.orderId
        })
        .then(res => {
          this.params = res.data.data;
          this.params.company_appraisal_goods_id = this.orderId;
          let s = [];

          String(res.data.data.symptom_id)
            .split(",")
            .forEach(val => {
              s.push(Number(val));
            });
          this.warehouseList = [
            {
              warehouse_name: this.params.warehouse_name,
              warehouse_id: this.params.warehouse_id
            }
          ];
          this.allocationInventoryList = [
            {
              allocation_name: this.params.allocation_name,
              allocation_id: this.params.allocation_id
            }
          ];
          this.params.symptom_id = s;
        });
    }
  }
};
</script>
